.row{
    margin-left:20px;
    color:white;
}
.row_posters{
    display:flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display:none;
}
.row_images{
    object-fit : contain;     /*maintains the aspect ratio*/    
    width:100%;
    max-height:100px;
    margin-right:10px;
    transition: transform 450ms;        /*slight delay in transition*/
}
 .row_images:hover{
     transform:scale(1.08);         /*makes the image a little large*/
 }
 .row_imageLarge{
     max-height:250px;
 }

 .row_imageLarge:hover{
    transform:scale(1.09);
   
 }